                        <tbody>
                            <tr v-for="(item) in items" :key='item["id"]'>
                                <td class="title">{{ item["No Langganan"] }}</td>
                                <td class="title">{{ item["NPWP"] }}</td>
                                <td class="title">{{ item["NPPKP"] }}</td>
                                <td class="title">{{ item["Nama Langganan"] }}</td>
                                <td class="title">{{ item["Alamat"] }}</td>
                                <td class="title">{{ item["Kota"]  }}</td>
                                <td class="title">{{ item["NIK"]  }}</td>
                                <td class="title">{{ item["RT"]  }}</td>
                                <td class="title">{{ item["RW"]  }}</td>
                                <td class="title">{{ item["Kelurahan"]  }}</td>
                                <td class="title">{{ item["Kecamatan"]  }}</td>
                                <td>
                                    <v-btn icon color="indigo" light @click='getDetail(item)'>
                                        <v-icon>mdi-eye</v-icon>
                                    </v-btn>
                                    <v-btn icon color="indigo" light @click="editHandler(item)">
                                        <v-icon>mdi-pencil</v-icon>
                                    </v-btn>
                                    <v-btn icon color="error" light @click='deleteData(item["No Langganan"])'>
                                        <v-icon>mdi-delete</v-icon>
                                    </v-btn>
                                </td>
                            </tr>
                        </tbody>
                    </template>
                </v-data-table>
            </v-container>
        </v-card>
