                                    <template slot="item" slot-scope="data">
                                        {{data.item["KodeDaerah"]}} - {{data.item["Nama Daerah"]}}
                                    </template>
                                </v-autocomplete>
                            </v-col>
                            <v-col cols="2" v-else>
                               <v-text-field dense single-line outlined class="title mr-4" label="No Langganan" v-model="form.noLangganan" readonly>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">NPWP</h3>
                            </v-col>
                            <v-col cols="2">
                               <v-text-field dense single-line outlined class="title mr-4" label="NPWP" v-model="form.npwp" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">NPPKP</h3>
                            </v-col>
                            <v-col cols="2">
                               <v-text-field dense single-line outlined class="title mr-4" label="NPPKP" v-model="form.nppkp">
                                </v-text-field>
                            </v-col>
