                                <h3 class="mt-2">Alamat</h3>
                            </v-col>
                            <v-col cols="2">
                               <v-text-field dense single-line outlined class="title mr-4" label="Alamat" v-model="form.alamat">
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Kota</h3>
                            </v-col>
                            <v-col cols="2">
                               <v-text-field dense single-line outlined class="title mr-4" label="Kota" v-model="form.kota">
                                </v-text-field>
                            </v-col>
