                        <hr>
                        <br>
                        <v-row no-gutters>
                            <v-col cols="1">
                                <h3 class="mt-2">Nama</h3>
                            </v-col>
                            <v-col cols="2">
                               <v-text-field dense single-line outlined class="title mr-4" label="Nama Pelanggan" v-model="form.namaPelanggan">
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Contact Person</h3>
                            </v-col>
                            <v-col cols="2">
                               <v-text-field dense single-line outlined class="title mr-4" label="Contact Person Pelanggan" v-model="form.contactPersonPelanggan">
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Alamat</h3>
                            </v-col>
                            <v-col cols="2">
                               <v-text-field dense single-line outlined class="title mr-4" label="Alamat Pelanggan" v-model="form.alamatPelanggan">
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Kota</h3>
                            </v-col>
                            <v-col cols="2">
                               <v-text-field dense single-line outlined class="title mr-4" label="Kota Pelanggan" v-model="form.kotaPelanggan">
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Kode Pos</h3>
                            </v-col>
                            <v-col cols="2">
                               <v-text-field dense single-line outlined class="title mr-4" label="Kode Pos Pelanggan" v-model="form.kodePosPelanggan">
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
