                                        {{data.item["Kode"]}} - {{data.item["Jenis"]}}
                                    </template>
                                </v-autocomplete>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Link Alamat</h3>
                            </v-col>
                            <v-col cols="2">
                               <v-text-field dense single-line outlined class="title mr-4" label="Link Pelanggan" v-model="form.linkPelanggan">
                                </v-text-field>
                            </v-col>
                        </v-row>
