                    <v-layout align-end justify-end>
                        <v-btn color="red white--text" dense rounded @click="close">Close</v-btn>
                        <v-btn class="ml-4 white--text" dense color="blue darken-1" rounded @click="setForm()" >Save</v-btn>
                    </v-layout>
                </v-container>
            </v-card>
        </v-dialog>
        <v-dialog v-model="dialogCat" persistent max-width="1000px">
            <v-card>
                <v-container grid-list-md mb-0>
                    <v-btn icon @click="dialogCat = false">
                        <v-icon>mdi-close</v-icon>
                    </v-btn>
                <h2 class="text-md-center mb-5">Data Detail LRelasiSemua</h2>
                <v-data-table :headers="headersDetail" :items="detail" :search="keyword" :loading="load">
                    <template v-slot:body="{ items }">
                        <tbody>
                            <tr v-for="(item) in items" :key='item.id'>
                                <td class="title">{{ item["Nama Pelanggan"] }}</td>
                                <td class="title">{{ item["Contact Person Pelanggan"] }}</td>
                                <td class="title">{{ item["Alamat Pelanggan"] }}</td>
                                <td class="title">{{ item["Kota Pelanggan"] }}</td>
                                <td class="title">{{ item["Kode Pos Pelanggan"]  }}</td>
                                <td class="title">{{ item["Telephone Pelanggan"] }}</td>
                                <td class="title">{{ item["Discount%"] }}</td>
                                <td class="title">{{ item["Tanggal Hubungan"] }}</td>
                                <td class="title">{{ item["Kondisi Pembayaran"] }}</td>
                                <td class="title">{{ item["NoHP Pelanggan"] }}</td>
                                <td class="title">{{ item["Jenis Pelanggan"] }}</td>
                                <td class="title">{{ item["Link Pelanggan"] }}</td>
                            </tr>
                        </tbody>
                    </template>
                </v-data-table>
            </v-container>
            </v-card>
        </v-dialog>
        <v-snackbar v-model="snackbar" :color="color" :multi-line="true" :timeout="3000">
            {{ text }}
            <v-btn dark text @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                jenisPelanggan: [],
                form: {
                    noLangganan : "",
                    npwp : "00.000.000.0-000.000",
                    nppkp : "00.000.000.0-000.000",
                    namaLangganan : "",
                    alamat : "",
                    kota : "",
                    nik : "",
                    rt : "",
                    rw : "",
                    kelurahan : "",
                    kecamatan : "",
                    namaPelanggan : "",
                    contactPersonPelanggan : "",
                    alamatPelanggan : "",
                    kotaPelanggan: "",
                    kodePosPelanggan : "",
                    telephonePelanggan : "",
                    discountPersen : 0,
                    tanggalHubungan : "",
                    kondisiPembayaran : 0,
                    noHPPelanggan : "",
                    jenisPelanggan : "",
                    linkPelanggan : "",
                },
                dialogCat: false,
                detail: [],
                daerah: [],
                date: new Date().toISOString().substr(0, 10),
                menu: false,
                dialog: false,
                keyword: '',
                headersDetail: [
                    {
                        text: 'Nama Pelanggan',
                        value: 'Nama Pelanggan',
                        class: 'title',
                    },
                    {
                        text: 'Cotact Pelanggan',
                        value: 'Cotact Person Pelanggan',
                        class: 'title',
                    },
                    {
                        text: 'Alamat Pelanggan',
