                        class: 'title',
                    },
                    {
                        text: 'Telephone Pelanggan',
                        value: 'Telephone Pelanggan',
                        class: 'title',
                    },
                    {
                        text: 'Discount(%)',
                        value: 'Discount%',
                        class: 'title',
                    },
                    {
                        text: 'Tanggal Hubungan',
                        value: 'Tanggal Hubungan',
                        class: 'title',
                    },
                    {
                        text: 'Kondisi Pembayaran',
                        value: 'Kondisi Pembayaran',
                        class: 'title',
                    },
                    {
                        text: 'NoHP Pelanggan',
                        value: 'NoHP Pelanggan',
                        class: 'title',
                    },
                    {
                        text: 'Jenis Pelanggan',
                        value: 'Jenis Pelanggan',
                        class: 'title',
                    },
                    {
                        text: 'Link Pelanggan',
                        value: 'Link Pelanggan',
                        class: 'title',
                    },
                ],
                headers: [
                    {
                        text: 'No Langganan',
                        value: 'No Langganan',
                        class: 'title',
                    },
                    {
                        text: 'NPWP',
                        value: 'NPWP',
                        class: 'title',
                    },
                    {
                        text: 'NPPKP',
                        value: 'NPPKP',
                        class: 'title'
                    },
                    {
                        text: 'Nama Langganan',
                        value: 'Nama Langganan',
                        class: 'title'
                    },
                    {
                        text: 'Alamat',
                        value: 'Alamat',
                        class: 'title'
                    },
                    {
                        text: 'Kota',
                        value: 'Kota',
                        class: 'title'
                    },
                    {
                        text: 'NIK',
                        value: 'NIK',
                        class: 'title'
                    },
                    {
                        text: 'RT',
                        value: 'RT',
                        class: 'title'
                    },
                    {
                        text: 'RW',
                        value: 'RW',
                        class: 'title'
                    },
                    {
                        text: 'Kelurahan',
                        value: 'Kelurahan',
                        class: 'title'
                    },
                    {
                        text: 'Kecamatan',
                        value: 'Kecamatan',
                        class: 'title'
                    },
                    {
                        text: 'Aksi',
                        value: '',
                        class: 'title'
                    },
                   
                ],
                snackbar: false,
                color: null,
                text: '',
                load: false,
                typeInput: 'new',
                errors: '',
                updatedId: '',
                statusUpdate : false,
                lrelasiSemua: [],
                lrelasi: new FormData,
               }
        },
        methods: {
            updateData(){
                this.lrelasi.append('noLangganan', this.form.noLangganan);
                this.lrelasi.append('npwp', this.form.npwp);
                this.lrelasi.append('nppkp', this.form.nppkp);
                this.lrelasi.append('namaLangganan', this.form.namaLangganan);
                this.lrelasi.append('alamat', this.form.alamat);
                this.lrelasi.append('kota', this.form.kota);
                this.lrelasi.append('nik', this.form.nik);
                this.lrelasi.append('rt', this.form.rt);
                this.lrelasi.append('rw', this.form.rw);
                this.lrelasi.append('kelurahan', this.form.kelurahan);
                this.lrelasi.append('kecamatan', this.form.kecamatan);
                this.lrelasi.append('namaPelanggan', this.form.namaPelanggan);
                this.lrelasi.append('contactPersonPelanggan', this.form.contactPersonPelanggan);
                this.lrelasi.append('alamatPelanggan', this.form.alamatPelanggan);
                this.lrelasi.append('kotaPelanggan', this.form.kotaPelanggan);
                this.lrelasi.append('kodePosPelanggan', this.form.kodePosPelanggan);
                this.lrelasi.append('telephonePelanggan', this.form.telephonePelanggan);
                this.lrelasi.append('discountPersen', this.form.discountPersen);
                this.lrelasi.append('tanggalHubungan', this.form.tanggalHubungan);
                this.lrelasi.append('kondisiPembayaran', this.form.kondisiPembayaran);
                this.lrelasi.append('noHPPelanggan', this.form.noHPPelanggan);
                this.lrelasi.append('jenisPelanggan', this.form.jenisPelanggan);
                this.lrelasi.append('linkPelanggan', this.form.linkPelanggan);
                var uri = this.$apiUrl + 'lrelasisemuas/' + this.updatedId
                // this.load = true
                this.$http.post(uri, this.lrelasi).then(response => {
                    this.snackbar = true; //mengaktifkan snackbar
                    this.color = 'green'; //memberi warna snackbar
                    this.text = 'Success'; //memasukkan pesan ke snackbar
                    // this.load = false;
                    this.dialog = false
                    this.statusUpdate = false
                    this.getData(); //mengambil data user
                    this.resetForm();
                    // this.Activity('Melakukan Penambahan Data Parameter Uji CPKB');
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                })
            },
            editHandler(item) {
                this.typeInput = 'edit';
                this.statusUpdate = true
                this.dialog = true;
                this.form.noLangganan =item["No Langganan"]
                this.form.npwp =item["NPWP"]
                this.form.nppkp =item["NPPKP"]
                this.form.namaLangganan =item["Nama Langganan"]
                this.form.alamat =item["Alamat"]
                this.form.kota =item["Kota"]
                this.form.nik =item["NIK"]
                this.form.rt =item["RT"]
                this.form.rw =item["RW"]
                this.form.kelurahan =item["Kelurahan"]
                this.form.kecamatan =item["Kecamatan"]
                this.form.namaPelanggan =item["Nama Pelanggan"]
                this.form.contactPersonPelanggan =item["Contact Person Pelanggan"]
                this.form.alamatPelanggan =item["Alamat Pelanggan"]
                this.form.kotaPelanggan =item["Kota Pelanggan"]
                this.form.kodePosPelanggan =item["Kode Pos Pelanggan"]
                this.form.telephonePelanggan =item["Telephone Pelanggan"]
                this.form.discountPersen =item["Discount%"]
                this.form.tanggalHubungan =item["Tanggal Hubungan"]
                this.form.kondisiPembayaran =item["Kondisi Pembayaran"]
                this.form.noHPPelanggan =item["NoHP Pelanggan"]
                this.form.jenisPelanggan =item["Jenis Pelanggan"]
                this.form.linkPelanggan =item["Link Pelanggan"]
                this.updatedId = item["No Langganan"];
                // console.log(this.form)
            },
            getDetail(item){
                this.detail = []
                this.detail.push(item)
                this.dialogCat = true
            },
            post(){
                this.lrelasi.append('noLangganan', this.form.noLangganan);
                this.lrelasi.append('npwp', this.form.npwp);
                this.lrelasi.append('nppkp', this.form.nppkp);
                this.lrelasi.append('namaLangganan', this.form.namaLangganan);
                this.lrelasi.append('alamat', this.form.alamat);
                this.lrelasi.append('kota', this.form.kota);
                this.lrelasi.append('nik', this.form.nik);
                this.lrelasi.append('rt', this.form.rt);
                this.lrelasi.append('rw', this.form.rw);
                this.lrelasi.append('kelurahan', this.form.kelurahan);
                this.lrelasi.append('kecamatan', this.form.kecamatan);
                this.lrelasi.append('namaPelanggan', this.form.namaPelanggan);
                this.lrelasi.append('contactPersonPelanggan', this.form.contactPersonPelanggan);
                this.lrelasi.append('alamatPelanggan', this.form.alamatPelanggan);
                this.lrelasi.append('kotaPelanggan', this.form.kotaPelanggan);
                this.lrelasi.append('kodePosPelanggan', this.form.kodePosPelanggan);
                this.lrelasi.append('telephonePelanggan', this.form.telephonePelanggan);
                this.lrelasi.append('discountPersen', this.form.discountPersen);
                this.lrelasi.append('tanggalHubungan', this.form.tanggalHubungan);
                this.lrelasi.append('kondisiPembayaran', this.form.kondisiPembayaran);
                this.lrelasi.append('noHPPelanggan', this.form.noHPPelanggan);
                this.lrelasi.append('jenisPelanggan', this.form.jenisPelanggan);
                this.lrelasi.append('linkPelanggan', this.form.linkPelanggan);
                var uri = this.$apiUrl + 'lrelasisemuas'
                // this.load = true
                this.$http.post(uri, this.lrelasi).then(response => {
                    this.snackbar = true; //mengaktifkan snackbar
                    this.color = 'green'; //memberi warna snackbar
                    this.text = 'Success'; //memasukkan pesan ke snackbar
                    // this.load = false;
                    this.dialog = false
                    this.statusUpdate = false
                    this.getData(); //mengambil data user
                    this.resetForm();
                    // this.Activity('Melakukan Penambahan Data Parameter Uji CPKB');
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                })
            },
            getData(){
                var uri = this.$apiUrl + 'lrelasisemuas'
                this.$http.get(uri).then(response => {
                    this.lrelasiSemua = response.data
                    this.getDataJenisPelanggan()
                    this.getDataDaerah()
                })
            },
            getDataDaerah(){
                var uri = this.$apiUrl + 'daerah'
                this.$http.get(uri).then(response => {
                    this.daerah = response.data
                })
            },
            getDataJenisPelanggan(){
                var uri = this.$apiUrl + 'jenispelanggans'
                this.$http.get(uri).then(response => {
                    this.jenisPelanggan = response.data
                })
            },
            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },

            deleteData(deleteId) { //mengahapus data
                var uri = this.$apiUrl + 'lrelasisemuas/' + deleteId; //data dihapus berdasarkan id
                this.$http.delete(uri).then(response => {
                    this.snackbar = true;
                    this.text = response.data;
                    this.color = 'green'
                    this.deleteDialog = false;
                    this.getData();
                    // this.Activity('Melakukan Delete Mutasi Produksi CPKB');
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                })
            },
            setForm() {
                if (this.typeInput === 'new') {
                    this.post()
                } else {
                    this.updateData()
                }
            },
            close() {
                this.dialog = false
                this.statusUpdate = false
                this.resetForm()
                this.typeInput = 'new'
            },
            resetForm() {
                this.typeInput = 'new'
                form = {
                    noLangganan : "",
                    npwp : "00.000.000.0-000.000",
                    nppkp : "00.000.000.0-000.000",
                    namaLangganan : "",
                    alamat : "",
                    kota : "",
                    nik : "",
                    rt : "",
                    rw : "",
                    kelurahan : "",
                    kecamatan : "",
                    namaPelanggan : "",
                    contactPersonPelanggan : "",
                    alamatPelanggan : "",
                    kotaPelanggan: "",
                    kodePosPelanggan : "",
                    telephonePelanggan : "",
                    discountPersen : 0,
                    tanggalHubungan : "",
                    kondisiPembayaran : 0,
                    noHPPelanggan : "",
                    jenisPelanggan : "",
                    linkPelanggan : "",
                }
            }
        },
        created() {
            this.getData()
        },
    }
</script>